
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import AttendanceList from '@/components/attendance/AttendanceList'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'PrincipalStudentList',
components: {
Group,
Cell,
AttendanceList,
Flexbox,
FlexboxItem,
LingLoading
},
data() {
return {
showContent: [],
titleArr: [
'姓名', '到校时间', '离校时间', '接收人', ''
],
attendanceLists: [
[
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'}
],
[
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'}
],
[
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'}
]
]
}
},
methods: {
/**
* 更新某班级列表的显示状态
* @param  {Number} index 出勤信息List的index
*/
changeShow: function(index) {

this.$store.commit('mutationClassesShowContent', this.changeClassesShowContentArr(index))
},
handleDetail: function() {
return

},
/**
* 组装班级的出勤信息
* @param  {Array} attendanceList 某班级出勤信息
* @return {Array}                组装后的出勤信息
*/
getAttendanceList: function(attendanceList) {
let arr = []
for(let i in attendanceList.studentDate) {
arr.push({
name: attendanceList.studentDate[i].name,
arrive: attendanceList.studentDate[i].arrive?attendanceList.studentDate[i].arrive:'****',
leave: attendanceList.studentDate[i].leave?attendanceList.studentDate[i].leave:'****',
parent: attendanceList.studentDate[i].parent?attendanceList.studentDate[i].parent:'****',
})
}
return arr
},
/**
* 初始化班级显示的状态
* @returns {Array}
*/
getClassesShowContentArr: function() {

let arr = []
for(let i = 0; i < this.getStudentAttendanceBySchool.num; i++) {
i==0?arr.push(true):arr.push(false)
}
return arr
},
/**
* 更改班级的显示状态，根据index
* @param index 更改的班级列表的序号
* @returns {Array} 更新之后的状态数组 最多显示一个班级
*/
changeClassesShowContentArr: function(index) {

let arr = []
for(let i = 0; i < this.getClassesShowContent.length; i++) {
i==index?arr.push(!this.getClassesShowContent[i]):arr.push(false)
}
return arr
},
fetchData: function() {

this.$store.dispatch('actionStudentAttendanceBySchool', {schoolId: 10, date: '2017-03-06'})
}
},
computed: {

getStudentAttendanceBySchool: function() {
return this.$store.getters.getStudentAttendanceBySchool
},

getClassesShowContent: function() {
return this.$store.getters.getClassesShowContent
},
/**
* 获取，取数据时的状态
* @returns {getLingLoading}
*/
getLoadingState: function() {
return this.$store.getters.getLingLoading
},
/**
* 获取选择时间
* @returns {*}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
},
},
watch: {
/**
* 监听学生列表变化,更新班级显示状态
*/
getStudentAttendanceBySchool: function() {

this.$store.commit('mutationClassesShowContent', this.getClassesShowContentArr())
},
/**
* 监听选择时间
* @param val
*/
getSelectTime: function(val) {
window.console.log(val)
}
},
created() {
this.fetchData()
}
}



